<template>
  <div id="basic">
    <vue-particles
      id="particles-js"
      color="#409eff"
      :particleOpacity="0.7"
      :particlesNumber="100"
      shapeType="circle"
      :particleSize="4"
      linesColor="#000"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="2"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push"
      class="lizi"
    >
    </vue-particles>
    <img
      src="https://wqby-1304194722.cos.ap-nanjing.myqcloud.com/img/logo.jpg"
      style="width:150px;height:150px"
    />
    <h2>CCIT杀手,让大学生没有难应付的高校形式主义</h2>

    <h4>作者：王权霸业 QQ：9479421</h4>
    <a-layout>
      <a-layout-header class="bgwhite">
        <Header></Header>
      </a-layout-header>
      <a-layout-content>
        <a-row class="bgwhite">
          <a-col :span="8">
            <Update />
          </a-col>
          <a-col :span="16">
            <router-view />
          </a-col>
        </a-row>
      </a-layout-content>
      <a-layout-footer class="bgwhite"><Footer /></a-layout-footer>
    </a-layout>
  </div>
</template>

<script>
import Header from "./Header";
import Footer from "./Footer";
import Update from "./Update";

export default {
  data() {
    return {};
  },
  components: {
    Header,
    Update,
    Footer
  }
};
</script>

<style scoped>
.bgwhite {
  background-color: white;
  opacity: 0.8;
}
#basic {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#particles-js {
  width: 100%;
  height: calc(100% - 100px);
  position: absolute;
}

</style>



